<template>
    <div id="dashboard" class="pt-5">
        <div class="card">
            <div class="card-header">
                <h4>Employees</h4>
            </div>
            <table class="table table-striped">
                <thead>
                    <tr>
                    <th scope="col">ID</th>
                    <th scope="col">Name</th>
                    <th scope="col">Department</th>
                    <th scope="col">Edit</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="employee in employees" v-bind:key="employee.id">
                        <th scope="row">{{employee.employee_id}}</th>
                        <td>{{employee.name}}</td>
                        <td>{{employee.dept}}</td>
                        <td><router-link v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}" class="btn btn-outline-primary">view</router-link></td>
                    </tr>
                </tbody>
            </table>
            <div class="card-footer">
                <router-link to="/new" class="btn btn-success">Add</router-link>
            </div>
        </div>

    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            employees: []
        }
    },
    created () {
        db.collection('employees').orderBy('employee_id').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position
                }
                this.employees.push(data)
            })
        })
    }
}
</script>


