<template>
    <div id="view-employee" class="pt-5">
        <div class="card">
            <div class="card-header">
                <h4>{{name}}</h4>
            </div>
            <ul class="list-group list-group-flush">
                <li class="list-group-item">Employee Id : {{employee_id}}</li>
                <li class="list-group-item">Department : {{dept}}</li>
                <li class="list-group-item">Position : {{position}}</li>
            </ul>
            <div class="card-body">
                <router-link to="/" class="btn btn-outline-dark">Back</router-link>
                <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn btn-primary">Edit</router-link>
                <button @click="deleteEmployee" class="btn btn-danger">Delete</button>
            </div>

            <div class="fixed-action-btn">
                <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-larg red">
                    <i class="fa fa-pencil"></i>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-employee',
    data () {
        return {
            employee_id: null,
            name: null,
            dept: null,
            position: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('employees')
        .where('employee_id', '==', to.params.employee_id)
        .get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee_id = doc.data().employee_id 
                    vm.name = doc.data().name 
                    vm.dept = doc.data().dept 
                    vm.position = doc.data().position 
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('employees')
            .where('employee_id', '==', this.$route.params.employee_id)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.employee_id = doc.data().employee_id 
                    this.name = doc.data().name 
                    this.dept = doc.data().dept 
                    this.position = doc.data().position 
                })
            })
        },
        deleteEmployee () {
            if(confirm('Sure?')) {
                db.collection('employees')
                .where('employee_id', '==', this.$route.params.employee_id)
                .get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        },
        
    }
}
</script>


