
<template>
    <div id="playboard" class="pt-5">
                <svg id="picture" viewBox="-250 -250 500 500">
                    <!--
                    <template v-for="j in (0, numj)">
                        <template v-for="i in (0, numi)">
                            <circle :key="j*num+i"
                                    :cx="(i-numi/2)*(nrad+1)*2"
                                    :cy="(j-numj/2)*(nrad+1)*2"
                                    :r="nrad"
                                    fill="#ccc"
                                    :stroke="color"
                                    v-on:click="handleClick(key)"/>
                        </template>
                    </template>
                    -->
                    <template v-for="p in grid">
                                <circle :key="p.id"
                                    :cx="(p.x-numj/2)*(nrad + 1)*2"
                                    :cy="(p.y-numj/2)*(nrad + 1)*2"
                                    :r="nrad"
                                    :fill="p.c"
                                    :stroke="color"
                                    v-on:click="handleClick(p)"/>
                    </template>
                </svg>     

    </div>
</template>

<script>
import tinycolor2 from 'tinycolor2'
import * as d3 from 'd3'
import _ from 'lodash'
import db from './firebaseInit'
export default {
    name: 'playboard',
    data () {
        return {
            employees: [],
            nrad:   6,
            color:  '#aaa',
            numi:    20,
            numj:    20,
            nindex:  0,
            grid: []
        }
    },
    created () {
        db.collection('grid').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                var tinycolor = require("tinycolor2");
                var colorInstruction = "hsl(" + String(240+(120*doc.data().v)) + ", 100%, 50%)";
                var hex = tinycolor(colorInstruction).toHexString();
                console.log(hex)
                const data = {
                    'id': doc.data().id,
                    'x': doc.data().x,
                    'y': doc.data().y,
                    'v': doc.data().v,
                    'c': hex
                }
                this.grid.push(data)
                console.log(doc.data().x,doc.data().y)
            })
        })
    },
    methods: {
        cd: function(v) {
            color = tinycolor("hsl(240+(v*120), 100%, 50%)");
            return color.toHexString();
        },
        handleClick: function(p) {
            p.v = 1.0 - p.v;
            var tinycolor = require("tinycolor2");
            var colorInstruction = "hsl(" + String(240+(120*p.v)) + ", 100%, 50%)";
            var hex = tinycolor(colorInstruction).toHexString();
            p.c = hex;
            db.collection('grid')
            .where('id', '==', p.id)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        id: p.id,
                        x: p.x,
                        y: p.y,
                        v: p.v
                    })                    
                })
            })
        },
        setGrid () {
            var ii,jj,id;
            id = 0;
            for (ii = 0; ii < this.numi; ii++) { 
                for (jj = 0; jj < this.numj; jj++) { 
                    id += 1;
                    console.log(ii,jj)
                    db.collection('grid').add({
                    id: id,
                    x: ii,
                    y: jj,
                    v: 0.0           
                    })
                }
            }
            
        }
    }
}
</script>

<style>
#picture {
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  position: absolute;
  max-height:20cm;
  max-width:40cm;
  width: 100%;
  height: 100%;
}
path, circle {
  transition: all 500ms ease;
}
</style>



